import React from 'react';
import styled from 'styled-components';
import {MainLayout, InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import PrimaryButton from '../Components/PrimaryButton';
import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import ContactItem from '../Components/ContactItem';

function ContactPage() {
    const phone = <PhoneIcon />
    const email = <EmailIcon />
    const location = <LocationOnIcon />
    const isValid = (field) => (field != null && field.length > 0);
    const destinationEmail = "mahesh.jindal6@gmail.com";

    const handleSubmit= (e) => {
        e.preventDefault();
        const formData = e.target.elements;
        const name = formData["name"];
        const email = formData["email"];
        const subject = formData["subject"];
        const body = formData["textarea"];
        if(isValid(name.value) && isValid(email.value)
        && isValid(subject.value) && isValid(body.value)){
            let action = "mailto:"+destinationEmail+"?subject="+encodeURIComponent(subject.value)+"&body="
            + encodeURIComponent(body.value)+"%0D %0D From: "+name.value + "%0D Email: "+email.value;
            window.open(action, "_self")
        }else{
            alert('Name / Email / Subject / Body can not be empty');
        }
      }
  
    return (
        <MainLayout>
            <Title title={'Contact'} span={'Contact'} />
            <ContactPageStyled >
            <InnerLayout className={'contact-section'}>
                <div className="left-content">
                    <div className="contact-title">
                        <h4>Get In Touch</h4>
                    </div>
                    <form className="form" onSubmit={e => { handleSubmit(e) }}>
                        <div className="form-field">
                            <label htmlFor="name"  >Enter your name*</label>
                            <input type="text" id="name" required/>
                        </div>
                        <div className="form-field">
                            <label htmlFor="email"  >Enter your email*</label>
                            <input type="email" id="email" required/>
                        </div>
                        <div className="form-field">
                            <label htmlFor="subject"  >Enter your subject*</label>
                            <input type="text" id="subject" required/>
                        </div>
                        <div className="form-field">
                            <label htmlFor="text-area">Enter your Message*</label>
                            <textarea name="textarea" id="textarea" cols="30" rows="10" required></textarea>
                        </div>
                        <div className="form-field f-button">
                            <button type="submit"><PrimaryButton title={'Send Email'} /></button>
                        </div>
                    </form>
                </div>
                <div className="right-content">
                    <ContactItem title={'Phone'} icon={phone} cont1={'(+1) 917 349-4687'} />
                    <ContactItem title={'Email'} icon={email} cont1={'mahesh.jindal6@gmail.com'} />
                    <ContactItem title={'Address'} icon={location} cont1={'New York, NY'} cont2={'United States'} />
                    
                </div>
            </InnerLayout>
            </ContactPageStyled>
        </MainLayout>
    )
}

const ContactPageStyled = styled.section`
    .contact-section{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 2rem;
        @media screen and (max-width: 978px){
            grid-template-columns: repeat(1, 1fr);
            .f-button{
                margin-bottom: 3rem;
            }
        }
        .right-content{
            display: grid;
            grid-template-columns: repeat(1, 1fr);
            @media screen and (max-width: 502px){
                width: 70%;
            }
        }
        .contact-title{
            h4{
                color: var(--white-color);
                padding: 1rem 0;
                font-size: 1.8rem;
            }
        }
        .form{
            width: 100%;
            @media screen and (max-width: 502px){
                width: 100%;
            }
            .form-field{
                margin-top: 2rem;
                position: relative;
                width: 100%;
                label{
                    position: absolute;
                    left: 20px;
                    top: -19px;
                    display: inline-block;
                    background-color: var(--background-dark-color);
                    padding:0 .5rem;
                    color: inherit;
                }
                input{
                    border: 1px solid var(--border-color);
                    outline: none;
                    background: transparent;
                    height: 50px;
                    padding:0 15px;
                    width: 100%;
                    color: inherit;
                }
                textarea{
                    background-color: transparent;
                    border: 1px solid var(--border-color);
                    outline: none;
                    color: inherit;
                    width: 100%;
                    padding: .8rem 1rem;
                }
            }

            button{
                border:none;
            }
        }
    }
`;

export default ContactPage
